	module.exports = {"data":{"markdownRemark":{"html":"<p>At 12 o'clock, the city sounded a deafening sound of firecrackers. Watching on the 10th floor, there is a special state of mind. This year's firecrackers are smaller than last year and probably become more and more civilized. Guangzhou, Beijing and other places have banned firecrackers. Shanghai has not yet implemented it resolutely, only propaganda.</p>\n<p>Seeing some high-end fireworks, small, probably very expensive. It can also be said that people's living standards are improving. We put a thousand firecrackers, and we are not civilized. On such occasions, people tend to have a feeling of sadness. The more they live, the more they feel the existence of their own life, the stronger external strength that the world feels, and the ability to exist without feelings and emotions. Strength is the power that can exist without depending on human reason and ideal. In a plain life, people often forget themselves and feel that they are integrated into society and the world. However, when the world boiled up, it was discovered that it was lonely and quiet.</p>\n<p>Playing a video game called \"Monopoly\", playing for a while, buying land, stocks, building malls, finally bankrupt, lost to a man named \"Da Qian Qian\", chased by the dog Escape, fall into the gutter.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0029.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0029.md","frontmatter":{"title":"Thursday, February 10"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0029.md absPath of file >>> MarkdownRemark"}}

